footer{
background-color: #0f3460;
padding: 100px 0;
color:#fff;
}
footer h1{
    margin-bottom: 30px;
    color:#e94560;
    font-weight: 500;
    font-style: italic;

}
footer .img{
    background-color:#0c2a4d;
    padding:15px 20px;
    border-radius:5px;
}
footer .img i{
    font-size:20px;
    margin:5px;
}
footer .img p{
  margin-top: 5px;
}
footer p{
    font-size:14px;
    font-weight:300 ;
    opacity:0.5;
    margin-bottom:20px;
}
footer h2{
    margin-bottom: 20px;
}
footer li{
    margin-bottom: 10px;
}