.topCart{
    position:relative;
}
.topCart .box{
    margin: 0 20px;
}
.topCart img{
    width:100%;
    height:100%;
}
.topCart .nametop{
    position:relative;
    width:100%;
}
 span{
    background-color:#0f3460;
    color:#fff;
    font-size:12px;
    padding:5px 10px;
    border-radius: 50px;
    margin:5px;
    position:absolute;
    top:0;
}
.topCart .nametop .tleft{
left:0;
}
.topCart .nametop .tright{
    background-color: aliceblue;
    color:#000;
    position:absolute;
    right:0;
}